import React,{Component} from 'react';
import './about.css'
import propTypes from 'prop-types';
import {connect} from 'react-redux';
import {getInfoData, getExpreData} from "./../../store/actions/action";

class About extends Component{
    static propTypes = {
        aboutData:propTypes.object.isRequired,
        getExpreData:propTypes.func.isRequired,
        getInfoData:propTypes.func.isRequired,
    };
    state = {
        uid:940819
    };
    componentDidMount(){
        this.props.getInfoData({uid:this.state.uid});
        this.props.getExpreData();
    };
    render() {
        let expre = this.props.aboutData.expre||[];
        let expreItem = expre.map((item, i) => {
            return(
                <div key={i} className='item'>
                    <h4 className='time-spre'>{item.start+'至'+item.end}</h4>
                    <h3 className='item-tit'>{item.comp+' —— '+item.job}</h3>
                    <div className='item-desc'>{item.expre}</div>
                </div>
            )
        });
        let info = this.props.aboutData.myInfo;
        return (
            <div className='about-box rig-container'>
                <div className='user-info'>
                    <div className='about-img'>
                        <img src={require('./../../static/images/h.jpg')} alt='头像' />
                    </div>
                    <div className='info-txt'>
                        <p className='info-item'>
                            <span className='t'>Name：</span><span className='v'>{info.uname}</span>
                        </p>
                        <p className='info-item'>
                            <span className='t'>Birthday：</span><span className='v'>{info.birthday}</span>
                        </p>
                        <p className='info-item'>
                            <span className='t'>Degree：</span><span className='v'>{info.degree}</span>
                        </p>
                        <p className='info-item'>
                            <span className='t'>Objective：</span><span className='v'>{info.objective}</span>
                        </p>
                        <p className='info-item'>
                            <span className='t'>Email：</span><span className='v'>{info.email}</span>
                        </p>
                        <p className='info-item tx2'>
                            <span className='t'>Profiles：</span><span className='v'>{info.profiles}</span>
                        </p>
                    </div>
                </div>
                <div className='express'>
                    <h2 className='tit'>个人履历：</h2>
                    <div className='expre-list'>
                        {expreItem}
                    </div>
                </div>
            </div>
        );
    }
}
export default connect(state=>({
    aboutData:state.aboutData
}),{
    getInfoData,
    getExpreData
})(About);